/*Есть файл apiService.js с дефолтным экспортом объекта отвечающего за логику HTTP-запросов к API */
export default { apiService };

function apiService(searchQuery) {
const page = 1;
const MY_KEY = '19150755-18ebc4fb910ab3d1add5e1d5a';
    return fetch(`https://pixabay.com/api/?image_type=photo&orientation=horizontal&q=${searchQuery}&page=${page}&per_page=12&key=${MY_KEY}`)
       
        .then(response => { return response.json(); })
        .catch(error => console.log(error));
    
        
}; 
