import './styles.css';
import API from './apiService';
import photoCard from './templates/photo-card.hbs'
import getRefs from './get-refs';

import { error } from "@pnotify/core";
import "@pnotify/core/dist/PNotify.css";
import "@pnotify/core/dist/BrightTheme.css";
import "@pnotify/confirm/dist/PNotifyConfirm.css";

const debounce = require('lodash.debounce');
const refs = getRefs();

refs.inputEl.addEventListener('input', debounce(onInputSearch, 1000));

function onInputSearch(e) {
    e.preventDefault();
    const searchQuery = refs.inputEl.value.trim();
    console.log(searchQuery);
     if (searchQuery !== '') {
    API.apiService(searchQuery)
        .then(refs.listEl.innerHTML = '')
        //.then(refs.photoCard.innerHTML = '')
        .then(e => renderPhotoCard(e.hits))
        .then(console.log(API.apiService(searchQuery)))
            .catch(onFetchError)
            .finally(() => { refs.inputEl.value === '' })
  }
};

function renderPhotoCard(photos) {
    if (photos.length !== 0) {
        const newListItem = photos.map((photo) => {
            console.log(photoCard(photo));
            const newPhoto = document.createElement("li");
            newPhoto.innerHTML = photoCard(photo);
            console.log(newPhoto);
            refs.listEl.appendChild(newPhoto);

            return newListItem;
        });
    } else click()
    };

function onFetchError(error) {
            clickError();
        };

function click() {
  error({
    text:
      "Error, no result found!!",
    delay: 700
  });
    };

function clickError() {
  error({
    text:
      "Error!!",
    delay: 700
  });
    };